import React from "react";
import {
  StatusRunning,
  StatusOK,
  StatusAborted,
  StatusError,
} from "@backstage/core-components";
import { DeploymentStatus as DeploymentStatusEnum } from "@aws-sdk/client-codedeploy";

export const DeploymentStatus = ({
  status,
}: {
  status: string | undefined;
}) => {
  switch (status) {
    case DeploymentStatusEnum.QUEUED:
    case DeploymentStatusEnum.IN_PROGRESS:
      return (
        <>
          <StatusRunning /> In progress
        </>
      );
    case DeploymentStatusEnum.FAILED:
      return (
        <>
          <StatusError /> Failed
        </>
      );
    case DeploymentStatusEnum.SUCCEEDED:
      return (
        <>
          <StatusOK /> Succeeded
        </>
      );
    case DeploymentStatusEnum.STOPPED:
      return (
        <>
          <StatusAborted /> Aborted
        </>
      );
    default:
      return (
        <>
          <StatusAborted /> Unknown
        </>
      );
  }
};
